import React, { useState } from "react";
import "primereact/resources/themes/bootstrap4-light-purple/theme.css";
import { InputTextarea } from "primereact/inputtextarea";
import { Button } from "primereact/button";
import QRCode from "qrcode";
import { Card } from "primereact/card";
import "./QrcodeStyle.css";

const QrCodeGen = () => {


  const [query, setQuery] = useState("");
  const [qrUrl, setQrUrl] = useState("");


  // qr code generate button
  const GenerateQrCode = async () => {
    try {
      const dataUrl = await QRCode.toDataURL(query);
      setQrUrl(dataUrl);
      setQuery("");
    } catch (e) {
      console.error(e);
    }
  };

  //   downloade button
  const downloadeQrCode = () => {
    try {
      const link = document.createElement("a");
      link.href = qrUrl;
      link.download = "qr_code.png"; // Added .png extension for clarity
      link.style.display = "none";

      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    } catch (e) {
      console.log("Failed to downloade");
    }
  };

  // delete button
  const deleteQrCode = () => {
    setQrUrl("");
    setQuery("");
  };

  return (
    <>
      <div className="Qr-code-Dev">
        <h1>Qr code Generator</h1>
        <InputTextarea placeholder="Enter here..."
          autoResize
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          rows={3}
          cols={30}
        />
        <br />
        <Button
         className="Qr-button"
          label="Generate QR code"
          onClick={GenerateQrCode}
        />

      </div>
      {qrUrl.length ? (
         <div className="Qr-card-dev">
             <Card
            title="Qr Code"
            style={{
              minWidth: "30vw",
              width: "fit-content",
              margin: "1vh auto",
            }}
          >
            <img src={qrUrl} alt="qrcode"  />

            <br />
            <button  className="Qr-button btn btn-success"  onClick={downloadeQrCode}>             
              Download QR Code
              </button>

            <br />
            <button className="Delete-btn  btn btn-danger"  onClick={deleteQrCode}  label="Delete Qr Code">
            Delete QR Code
              </button>
          </Card>
         </div>
        ) : (
          ""
        )}
    </>
  );
};
export default QrCodeGen;